<script>
import { getCurrentInstance } from 'vue';

const usevuetify = () => {
    const instance = getCurrentInstance()
    if (!instance) {
        throw new Error(`vuetify instance doesn't exist`)
    }
    return instance.proxy.$vuetify
}

//時間割のjsonファイルを読み込む
//あとでAPIから取得するようにするため、URLを記述する
// import jsonTimetable from `https://api.aitplus.hyouhyan.com/timetable/?id=${LoginPage.AikodaiId}&pass=${LoginPage.PassWord}`
import jsonTimetable from "@/assets/json/timetable.json"

export default {

    name: 'TimeTable',
    data: () => ({
        AikodaiId: 0,
        PassWord: 0,
        DayOfWeek: [
            { id: 0, title: '' },
            { id: 1, title: '月' },
            { id: 2, title: '火' },
            { id: 3, title: '水' },
            { id: 4, title: '木' },
            { id: 5, title: '金' },
