            const vuetify = usevuetify();
            return vuetify.display.xs;
        },
    },
    computed: {
        getCardHeight() {
            const vuetify = usevuetify();
            return vuetify.display.xs ? '100' : '100';
        },
        getCardWidth() {
            const vuetify = usevuetify();
            return vuetify.display.xs ? '80' : '110';
        },
    }
